export function g_Asf() {
  return (
    <>
      <div className="caf-wrapper">
        <p>
          Lallocation de soutien familial (ASF) est une aide financire verse aux parents isols qui lvent seuls un enfant ou dont le montant de la pension alimentaire est faible.
          <p>
            LASF est verse par la Caisse dallocations familiales (CAF) ou la Mutualit sociale agricole (MSA) selon la caisse dont dpend lassur.
          </p>
        </p>
        <h1>Qui peut bnficier de lallocation de soutien familial ?</h1>
        <span>
          Dans tous les cas, pour avoir droit  l'ASF, vous devez remplir l'ensemble des conditions suivantes :
          <ul>
            <li>
              Vivre seul(e)
            </li>
            <li>
              Rsider en France
            </li>
            <li>
              Avoir au moins 1 enfant  charge pour lequel l'autre parent ne participe plus  l'entretien depuis au moins 1 mois
            </li>
          </ul>
        </span>

      </div>
    </>
  )
}
