export function g_Paje() {
  return (
    <>
      <div className="caf-wrapper">

        <p>
          La prestation daccueil du jeune enfant (PAJE) est un dispositif comprenant plusieurs aides destines aux parents dun enfant venant de natre : prime  la naissance ou  ladoption
        </p>
        <h1>Que comprend la prestation daccueil du jeune enfant ?</h1>
        <span>
          La prestation daccueil du jeune enfant permettent de faire face aux dpenses ou de compenser une perte de revenus professionnels :
          <ul>
            <li>
              La prime  la naissance ou  l'adoption permet de faire face aux premires dpenses lies  la naissance ou  l'adoption d'un enfant et  son entretien.
            </li>
            <li>
              L'allocation de base en cas de naissance ou en cas d'adoption a pour but de faire face aux dpenses lies  la naissance ou  l'adoption d'un enfant,  son entretien et  son ducation.
            </li>
            <li>
              La prestation partage d'ducation de l'enfant (PrParE) permet  l'un ou aux 2 parents de rduire ou de cesser leur activit professionnelle pour s'occuper de leur enfant.
            </li>
            <li>
              Le complment de libre choix du mode de garde permet aux parents qui continuent  travailler de compenser le cot de la garde d'un enfant en cas d'emploi d'une assistante maternelle, d'une garde  domicile ou en micro-crche.
            </li>
          </ul>
        </span>

      </div>
    </>
  )
}

